  data () {
    return {
      // 設定表格行樣式
      cellStyleOption: {
        bodyCellClass: ({ row, column, rowIndex }) => {
          return 'table-body-cell-class'
        }
      },
      // 搜索
      searchText: '',
      tableTitle: [
        {
          field: '',
          key: 'a',
          title: 'No.',
          width: '3%',
          align: 'center',
          renderBodyCell: ({ row, column, rowIndex }, h) => {
            return (
              <span style="color:#7876B3;">
                {++rowIndex}
              </span>
            )
          }
        },
        // { field: '_id', key: 'a', title: 'Id', width: '5%' },
        {
          field: 'template',
          key: 'b',
          title: 'temp',
          width: '5%',
          align: 'center',
          fixed: 'left'
          // renderHeaderCell: ({ column }, h) => {
          //   return (
